	module.exports = {"data":{"markdownRemark":{"html":"<p>The less than equal to operator (<code>&#x3C;=</code>) compares the values of two numbers. If the number to the left is less than or equal the number to the right, it returns <code>true</code>. If the number on the left is greater than the number on the right, it returns <code>false</code>. Like the equality operator, <code>less than equal to</code> converts data types.</p>\n<h2>Examples</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>  4 <= 5  // true\n'7' <= 7  // true\n  5 <= 5  // true\n  3 <= 2  // false\n'8' <= 4  // false</code></pre>\n      </div>","fields":{"slug":"/certificates/comparison-with-the-less-than-or-equal-to-operator/"},"frontmatter":{"title":"Comparison with the Less Than or Equal to Operator"}}},"pathContext":{"slug":"/certificates/comparison-with-the-less-than-or-equal-to-operator/"}}

